.popup {
  width: 100%;
  height: 100%;
  z-index: 999;
  background-color: rgba(0, 0, 0, 0);
  transition: all .4s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateY(-44.5%)scale(0%);
}

.popup.active {
  background-color: #4f2ee8;
  transform: translateY(0%)scale(100%);
}

.popup-container {
  width: 100%;
  height: 100%;
  display: flex;
}

.popup-body {
  width: 580px;
  text-align: center;
  margin: auto;
  padding: 80px 15px 50px;
  position: relative;
}

@media screen and (max-width: 767px) {
  .popup-body {
    width: 335px;
    padding: 50px 20px 40px;
  }
}

.popup-close {
  cursor: pointer;
  font-size: 28px;
  position: absolute;
  top: 24px;
  right: 24px;
}

@media screen and (max-width: 767px) {
  .popup-close {
    font-size: 20px;
    top: 10px;
    right: 10px;
  }
}

.popup-body {
  background-color: #f3f3f3;
  border: 2px solid #111;
  border-radius: 18px;
}

.input {
  width: 500px;
  border: 2px solid #111;
  border-radius: 40px;
  margin: 0 auto 30px;
  padding: 25px;
}

@media screen and (max-width: 767px) {
  .input {
    width: 295px;
    padding: 16px;
  }
}

.button-signup {
  width: 500px;
  color: #f3f3f3;
  cursor: pointer;
  background-color: #111;
  border-radius: 40px;
  margin: 30px auto;
  padding: 20px;
  font-size: 22px;
}

@media screen and (max-width: 767px) {
  .button-signup {
    width: 320px;
    margin: 15px auto;
    padding: 16px;
    font-size: 18px;
  }
}

.svg-icon {
  fill: #111;
  width: 28px;
  height: 28px;
  position: absolute;
  top: 50%;
  right: 30px;
  transform: translateY(-50%);
}

@media screen and (max-width: 767px) {
  .svg-icon {
    width: 20px;
    height: 20px;
  }
}

.lebel {
  margin-top: 4px;
  position: relative;
}

.contact-form {
  margin: 0;
}

.link-wrapper {
  margin-top: 10px;
}

.link-signup {
  color: rgba(27, 26, 26, .5);
  margin-right: 30px;
  font-size: 22px;
  font-weight: 700;
}

@media screen and (max-width: 767px) {
  .link-signup {
    font-size: 18px;
  }
}

::-ms-input-placeholder {
  color: #111;
  font-size: 20px;
  font-weight: 500;
}

::placeholder {
  color: #111;
  font-size: 20px;
  font-weight: 500;
}

@media screen and (max-width: 767px) {
  ::-ms-input-placeholder {
    font-size: 16px;
  }

  ::placeholder {
    font-size: 16px;
  }
}

.link-signup.current {
  color: #4f2ee8;
}

